import React from 'react'
import './Producto.css'
import { Boton } from './Boton';


const Producto = ({url, name, price, discount}) => {
  return (
    <div className='producto'>
      <img className='producto__img' src={url} alt="" />
      <h2 className='producto__title'>{name}</h2>
      <div className="producto__details">
        <p className="producto__oldPrice">{price}$</p>
        <p className="producto__newPrice">{price - ((price * discount)/100)}$</p>
      </div>
      <Boton></Boton>
      <div className="producto__discount">{discount}%</div>
    </div>
  )
}

export { Producto };
