import React from 'react'
import { Contenedor } from './Components/Contenedor';
import { Producto } from './Components/Producto';
import data from './Productos.json';

 const App = () => {
  return (
    <main>
    
    <Contenedor>
      {data.map(element =>{
        return <Producto 
          url={element.url}
          name ={element.name}
          price ={element.price}
          discount = {element.discount}
        />
      })}
    </Contenedor>
    </main>
  )
}

export default App
